.list{
    width: 100%;
    margin-top: 10px;

    .listTittle{
        color: white;
        font-size: 20px;
        font-weight: 500;
        margin-left: 50px;
    }

    .wrapper{
        position: relative;

        .slideArrow{
            width: 50px;
            height: 100%;
            background-color: rgb(37, 36, 36, 0.5);
            color: bisque;
            position: absolute;
            z-index: 99;
            top: 0;
            bottom: 0;
            margin: auto;
            cursor: pointer;


        &.left{
            left: 0;
        }
        &.right{
            right: 0;
        }
        }
        .container{
            margin-left: 50px;
            display: flex;
            width: max-content;
            margin-top: 5px;
            transform: translateX(0px);
            transition: all 1s ease;
        }
    }
}